  clickTranscript = (startOffset) => (event) => {
    const selectedTime = startOffset;
    document.getElementById("aroll").currentTime = selectedTime/1000.0;
  }

  arollPlay = () => (event) => {
  	document.getElementById("aroll").play();
  }

  onKeyPress = () => (event) => {
    if(event.key === 'Enter'){
        //do something
        console.log("detected enter!")
    this.setState({broll_footage: [], loadingBroll: true})
    const thiz = this;
    const searchkey = thiz.state.searchkey;
    console.log(thiz.state.searchkey)
    const apiURL = 'https://adobesearch-stage.adobe.io/universal-search/v2/search';
    const token = thiz.state.token;
    const formData = {
      "q": searchkey, 
      "scope": ["stock"], 
      "asset_type": ["video"], 
      "limit": 12
    };
    fetch(apiURL, {
      method: 'POST',
      withCredentials: true,
      headers: {
        'Authorization': 'Bearer ' + token,
        'x-api-key': 'AdobeSearchService1',
        'x-product': 'Federer tests',
        'x-product-location': 'Federer container tests',
        'Content-Type': 'application/vnd.adobe.search-request+json'
      },
      body: JSON.stringify(formData)
    }).then((response) => response.json())
      .then((responseData) => { 
        var out = []
        var out2 = []
        for (var i = 0; i < responseData.result_sets[0].items.length;i++) {
          //console.log(responseData.result_sets[0].items[i]._links.rendition[0].href)
          out2.push(responseData.result_sets[0].items[i]._links.rendition[0].href)
          //console.log(responseData.result_sets[0].items[i]._links.original.href);
          out.push(responseData.result_sets[0].items[i]._links.original.href)
        }
        thiz.setState({broll_footage: out, loadingBroll: false, broll_thumbnail: out2})
      }).catch((err) => { 
        console.log("something went wrong!");
        thiz.setState({loadingBroll: false})
        console.log(err);
      });
    }
  }

